<template>
<div>
  <div v-show="showAdminDepoFinalDisplay===0">
    <div id="spinner" class="spinner-border text-success" role="status">
      <span class="sr-only">Loading...</span>
    </div>
  </div>

  <table id="table1" class="table" v-show="showAdminDepoFinalDisplay===1">
    <thead>
      <tr>
        <th colspan="2" scope="colgroup">
          Token Exchange Contract
        </th>
      </tr>
      <tr>
        <th scope="col">Token Name</th>
        <th scope="col">Token Balance</th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="key in param">
        <td>
          {{ key.tokenName }}
        </td>
        <td>
          {{key.tokenBalance}}
        </td>
      </tr>
    </tbody>

  </table>

  <img id="home_button" src="../assets/home_button.png" v-on:click="home" v-show="showAdminDepoFinalDisplay===1">
  <!-- modal -->
  <div>
      <app-admin-modal
        title="This is modal">
        <div id="modal_">
          {{this.$store.state.msg}}
        </div>
      </app-admin-modal>
  </div>
    <!--modal -->

</div>
</template>

<script>
import Web3 from 'web3';
import store from '../store';
import adminModal from './adminmodal.vue';

import {
  Admin_account_address,
  TokenExchange_address,
  TokenExchange_ABI,
  ERC20_ABI,
  EOS_tokenAddress,
  BNB_tokenAddress,
  VEN_tokenAddress,
  Token_ABI
} from './Contract'

let tokens = [EOS_tokenAddress, BNB_tokenAddress, VEN_tokenAddress];

if (typeof web3 !== 'undefined') {
  // Mist/MetaMask  
  web3 = new Web3(web3.currentProvider);
  // alert('web3 .');
} else {
  //  Metamask     
  //  Metamask     
  web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));
}

let walletAddress;

export default {

  data() {
    return {
      param: ''
    }
  },
  components: {
    appAdminModal: adminModal
  },
  created: function() {
    store.commit('changeAdminMsg',' .');
    store.commit('changeAdminModal');
    // store.commit('adminDepoFinalInitialization');
    // store.commit('changeAdminDepoFinalDisplay', 0);
  },

  computed: {
    showAdminDepoFinalDisplay: function() {
     return this.$store.state.adminDepoFinalShowDisplay;
   }
 },

  methods: {

    home: function(event) {
      store.commit('adminDepoFinalInitialization');
      store.commit('changeAdminDepoFinalDisplay',0);
      this.$router.push('/admin/home');
    },

    showContract: function() {

      walletAddress = TokenExchange_address;

      function getEther(callback) {
        return new Promise((resolve, reject) => {
          web3.eth.getBalance(walletAddress, (error, balance) => {
            if (!error) {
              let ether = web3.utils.fromWei(balance, 'ether'); //Push Ether : balance  to array
              store.commit('setAdminDepoFinalInfo',{
              tokenName : "ETHER",
              tokenBalance : ether
            });
              resolve()
            } else {
              console.error(" :  Ether    \n -    (web3    TokenExchange address )");
              store.commit('changeAdminMsg',' Ether    \n-    (web3    TokenExchange address )');
              store.commit('changeAdminModal');

              // alert(" :  Ether    \n -    (web3    TokenExchange address )");
              reject()
            }
          }); //end of getBalance
        }); //end of Promise
      } //end of getEther

      getEther()
        .then(() => { //then search ERC20 tokens

          const getTokenBalance = new Promise((resolve, reject) => {
            let ctn = 0;

            tokens.forEach((token) => {
              let contract = new web3.eth.Contract(ERC20_ABI, token);

              contract.methods.balanceOf(walletAddress).call({
                from: Admin_account_address
              }, (error, balance) => {
                contract.methods.decimals().call({
                  from: Admin_account_address
                }, (error, decimals) => {
                  if (!error) {
                    let calculatedResult = balance / (Math.pow(10, decimals));

                    contract.methods.name().call({
                      from: Admin_account_address
                    }, (error, tokenName) => {

                      ctn++;

                      if (balance.toString() != '0') {
                        store.commit('setAdminDepoFinalInfo',{
                        tokenName : tokenName,
                        tokenBalance : calculatedResult
                      });
                      }
                      if (ctn == tokens.length) {
                        ctn = 0;
                        resolve()
                      }
                    }); //end of name
                  } else {
                    console.error(" :  Token    \n -    ( Token address )");
                    store.commit('changeAdminMsg',' :  Token    \n-    ( Token address )');
                    store.commit('changeAdminModal');

                    // alert(" :  Token    \n -    ( Token address )");
                    console.log(error);
                  }
                }); //end of decimals
              }); //end of balanceof
            }); //end of tokens

          });

          getTokenBalance.then(result => {
            this.param = store.state.adminDepoFinalInfo;
            store.commit('changeAdminDepoFinalDisplay', 1);
          });
        });
    }
  },

  mounted() {
    this.showContract();
  }

}
</script>

<style>
th {
  background-color: #29B6F6;
  color: #FFFFFF;
  text-align: center;
}

td {
  text-align: center;
}

#table1 {
  width: 40%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#home_button {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 10%;
}

#spinner {
  position: fixed;
  top: 50%;
  left: 50%;
}
</style>



